.ccel-post-type__sidebar {
  padding: 30px 30px;
}
.ccel-post-type__sidebar .wp-block {
  margin: 0 !important;
}
.ccel-post-type__sidebar .wp-block:where(h1, h2, h3, h4, h5, h6) {
  margin-bottom: 10px !important;
}
.ccel-post-type__sidebar h1, .ccel-post-type__sidebar h2, .ccel-post-type__sidebar h3, .ccel-post-type__sidebar h4, .ccel-post-type__sidebar h5, .ccel-post-type__sidebar h6 {
  margin-top: 0;
  margin-bottom: 10px !important;
  text-transform: uppercase;
}

.taxonomy-ccel_lesson_tag > .wp-block-post-terms__separator,
.taxonomy-ccel_lo_tag > .wp-block-post-terms__separator {
  display: none;
}
.taxonomy-ccel_lesson_tag > a,
.taxonomy-ccel_lo_tag > a {
  background-color: #002145;
  border: none;
  color: white;
  list-style: none;
  padding: 0px 6px;
  text-align: center;
  text-decoration: none !important;
  margin: 2px 2px;
  border-radius: 5px;
  display: inline-block;
}
.taxonomy-ccel_lesson_tag .wp-block-post-terms__prefix,
.taxonomy-ccel_lesson_tag .wp-block-post-terms__suffix,
.taxonomy-ccel_lo_tag .wp-block-post-terms__prefix,
.taxonomy-ccel_lo_tag .wp-block-post-terms__suffix {
  display: none;
}

#learning_outcome_theme_list,
#learning_outcome_prerequisites_list,
#learning_outcome_prerequisites_list_to,
#learning_outcome_lesson_list,
#related_lessons_list {
  font-size: 0.9rem;
  margin-left: 0;
  margin-top: 0;
  padding-left: 20px;
}
#learning_outcome_theme_list a,
#learning_outcome_prerequisites_list a,
#learning_outcome_prerequisites_list_to a,
#learning_outcome_lesson_list a,
#related_lessons_list a {
  font-style: italic;
}